/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 26px;
  height: 16px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: var(--switch-bg);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 12px;
  width: 12px;
  right: 2px;
  bottom: 2px;
  background-color: var(--neutral);
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--invert-neutral);
}

input:focus + .slider {
  box-shadow: var(--focus-default);
}

input:checked + .slider:before {
  transform: translateX(-10px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

label.switch {
  margin-bottom: 0;
}

.slider.round:before {
  border-radius: 50%;
}

