.ql-table .ql-picker-options [data-value=insert-row-above]::before {
  content: "Insert Row Above";
}
.ql-table .ql-picker-options [data-value=insert-row-below]::before {
  content: "Insert Row Below";
}
.ql-table .ql-picker-options [data-value=insert-column-right]::before {
  content: "Insert Column Right";
}
.ql-table .ql-picker-options [data-value=insert-column-left]::before {
  content: "Insert Column Left";
}
.ql-table .ql-picker-options [data-value=delete-row]::before {
  content: "Delete Row";
}
.ql-table .ql-picker-options [data-value=delete-column]::before {
  content: "Delete Column";
}
.ql-table .ql-picker-options [data-value=delete-table]::before {
  content: "Delete Table";
}

.ql-editor td {
  border: 1px solid var(--dark-border-color);
}

.ql-editor blockquote {
  border-right: 4px solid var(--gray-300);
  padding-right: var(--padding-sm);
  font-size: var(--text-sm);
  font-weight: var(--weight-regular);
  letter-spacing: 0.02em;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}

.ql-editor.read-mode {
  height: unset;
  padding: 0;
}
.ql-editor.read-mode .mention {
  --user-mention-bg-color: var(--control-bg);
}

.ql-mention-list {
  border-radius: var(--border-radius-sm);
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 300px;
  overflow: scroll;
}

.mention > span {
  margin: 0 3px;
}

.mention[data-is-group=true] .icon, .mention[data-is-group=true] .es-icon {
  margin-top: -2px;
  margin-right: 4px;
}

.comment-input-wrapper .ql-editor.ql-blank::before {
  right: 8px;
  color: var(--text-muted);
  font-style: normal;
}

:root {
  --plyr-color-main: var(--primary);
}

.plyr {
  border-radius: var(--border-radius);
}
.plyr .plyr__poster {
  filter: brightness(0.7);
}

.version-info {
  overflow: auto;
}
.version-info pre {
  border: 0px;
  margin: 0px;
  background-color: inherit;
}
.version-info .table {
  background-color: inherit;
}
.version-info .success {
  background-color: var(--green-100) !important;
}
.version-info .danger {
  background-color: var(--red-100) !important;
}

[data-theme=dark] .version-info .danger,
[data-theme=dark] .version-info .success {
  color: var(--gray-900);
}
[data-theme=dark] .version-info .danger td,
[data-theme=dark] .version-info .success td {
  color: var(--gray-900);
}